/* Elements */

.button-fill {
  color: #171717;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border: 1px solid #e6ce76;
  background-color: #ffdc1c;
  border-radius: 50px;
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  z-index: 0;
  cursor: pointer;
}

.button-fill.submit {
  padding: 10px 35px;
  font-size: 1.4em;
}

.custom-hover {
  overflow: hidden;
}

.button-fill::before,
.custom-hover::before {
  transition: 0.5s;
  transition-delay: 0.1s;
  content: "";
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  display: block;
  width: 40px;
  height: 40px;
  background: #ffedaf;
  border-radius: 50px;
  z-index: 0;
}

.yellow-after::before {
  background: #ffedaf !important;
}

.button-fill:hover::before,
.custom-hover:hover::before {
  transform: scale(8.3);
}

.hover-underline:hover {
  text-decoration: underline;
}

.border-none.button-empty,
.border-none.button-fill {
  border: 0px solid #6c6c6c;
}

.button-empty {
  cursor: pointer;
  color: #171717;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border: 1px solid #6c6c6c;
  background-color: #fff;
  border-radius: 50px;
  display: inline-flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  z-index: 0;
}

.button-empty::before {
  transition: 0.5s;
  transition-delay: 0.1s;
  content: "";
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  display: block;
  width: 40px;
  height: 40px;
  background: #f9f8f8;
  border-radius: 50px;
}

.button-empty:hover::before {
  transform: scale(8.3);
}

.button-fill a,
.button-empty a {
  text-decoration: none;
  color: #171717;
}

